.m-result {
    height: 491px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #000;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    overflow: hidden;
    border-radius: 6px 6px 0 0;

    &::before {
        content: '';
        width: 100%;
        height: 1px;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #000;
    }

    &-planet {
        margin: 112px auto 0;
        width: 320px;
        height: 245px;
        background-size: 100% 100%;
        text-align: center;
        padding-top: 8px;
        box-sizing: border-box;
        position: relative;

        &-tips {
            color: #aecfff;
            font-size: 15px;
        }

        &-send {
            position: absolute;
            top: 50px;
            right: 54px;
            width: 71px;
            height: 75px;
            background-image: url('~$base/imgs/wpq.png');
            background-size: 100% 100%;
            color: #fff;
            font-weight: bold;
            font-size: 11px;
            line-height: 11px;
            padding: 13px 18px 0 28px;
            box-sizing: border-box;

            &-1 {
                background-image: url('~$base/imgs/ypq.png');
            }
        }

        &-winningusercount {
            font-size: 12px;
            line-height: 12px;
            margin-top: 7.23px;
            color: #898989;

            & span{
                color: #aecfff;
            }
        }
    }

    &-subtitle {
        text-align: center;
        margin-top: -12px;
        font-size: 16px;
        font-weight: bold;
        color: #fff;
        line-height: 16px;
    }

    &-gifts {
        margin-top: 6px;
        text-align: center;
        color: #addcff;
        font-size: 17px;
        padding: 0 38px;
    }

    &-footer {
        text-align: center;
        color: #fff;
        font-size: 14px;
        font-weight: bold;
        margin-top: 10px;

        &-button {
            margin: 0 auto;
            width: 221px;
            height: 34px;
            background-size: 100% 100%;
        }

        &-waitingbutton {
            margin: 0 auto;
            width: 221px;
            height: 34px;
            background-image: url('~$base/imgs/blackbtn.png');
            background-size: 100% 100%;
            line-height: 34px;
            font-weight: bold;
            font-size: 14px;
            color: #fff;
        }

        &-text {
            text-align: center;
            margin-top: 8px;
            font-size: 12px;
            color: #666;
        }
    }
}
